import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';

export interface ValuationInfo {
	address: string;
	bathroomCount?: any;
	bedroomCount?: any;
	county: string;
	countyArea: string;
	eirCode: string;
	email: string;
	fullname: string;
	propertySize: any;
	stand: any;
	roomTypeId: any;
	aisle: string;
	otherTenantShareCount?: any;
	section: any;
}

@Injectable({
	providedIn: 'root',
})
export class PropertyWorthState {
	private readonly _isProcessing$ = new BehaviorSubject(false);
	private readonly _valuationInfo$ = new BehaviorSubject<ValuationInfo>(null);
	private readonly _isError$ = new BehaviorSubject(false);
	private readonly _isLoading$ = new BehaviorSubject(true);

	get isError$() {
		return this._isError$.asObservable();
	}
	get ValuationInfo() {
		return this._valuationInfo$.asObservable();
	}

	get isLoading$() {
		return this._isLoading$.asObservable();
	}

	setErrorState(state: boolean): void {
		this._isError$.next(state);
	}

	setLoadingState(state: boolean): void {
		this._isLoading$.next(state);
	}

	get isProcessing$() {
		return this._isProcessing$.asObservable();
	}

	setProcessing(state: boolean): void {
		this._isProcessing$.next(state);
	}

	setValuationInfo(info: ValuationInfo): void {
		this._valuationInfo$.next(info);
	}

	getValuationInfo(): ValuationInfo {
		return this._valuationInfo$.getValue();
	}
}
